@import "./variables_background_images";
@import "./variables_color";

@mixin allBackgrondImagesSizing {
  position: static;
  background-position: center;   
  background-size: cover;
  background-repeat: no-repeat;
}
@mixin background_image_computerHardWare {
  background-image: $computerHardWare_background;
  @include allBackgrondImagesSizing;
}
@mixin background_image_laptops {
  background-image: $laptops_background;
  @include allBackgrondImagesSizing;
}
@mixin background_image_cart {
  background-image: $cart_background; 
  @include allBackgrondImagesSizing;
}

@mixin easeOut {
  transition: all 1s ease-out;
}

@mixin btn_buy {
  margin-left: 10px;
  padding: 2px;
  background: $primery_color;
  font-size: 12px;
  text-transform: uppercase;
  font-family: "Monda", sans-serif;
  border: 1px solid black;
  border-radius: 3px;
  color: black;
  text-decoration: none;
  font-weight: bold;
  cursor: pointer;

  &:hover {
    color: black;
    background-color: $btn_buy;
  }
}

@mixin btn_back {
  color: black;
  display: inline-block;
  text-decoration: none;
  font-weight: bold;
  font-size: 15px;
  padding: 7px;
  background: $header_background;
  border-radius: 3px;

  span {
    font-family: "Monda", sans-serif;
  }
}

@mixin nav_menu_components_phone_and_tablet {
  .big_box_components {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 0 2rem;
    border-radius: 0 0 5px 5px;
    background: $primery_color;
    .navProductsParrent {
      position: relative;
      height: auto;
      width: 220px;
      .link_menu {
        text-align: center; 
        a { 
          text-decoration: none;
          color: black;
          font-family: "Teko", sans-serif;
          font-size: 23px;
        }
      }
      .drop_down_menu_items {
        display: none;
        li {
          list-style: none;
          a {
            float: none;
            color: $header_links_color;
            text-decoration: none;
            padding: 10px;
            display: block;
            text-align: left;
            padding-left: 10px;
           
            .fas {
              pointer-events: none; 
              position: absolute;
              right: 3px;
              top: 9px;
              color: $header_background;
              font-size: 15px; 
            }
          }
        }
        .drop_down_menu_items_item {
          display: none;
        }
        .drop_down_menu_items_item {
          display: block;
          .btn_items_item {
            
            font-family: "Teko", sans-serif;
            width: 100%;
            color: $header_links_color;
            font-size: 18px;
            text-align: left;
            border: none;
            background: whitesmoke; 
            padding: 4px 0 0 15px; 
     
            cursor: pointer; 
            &:hover {
              background: rgba(176, 221, 233, 0.904);
            }
          }
        }
      } 
      .drop_down_menu_items {
        border: 1px solid $border; 
        border-radius: 3px;
        display: block;
        display: flex; 
        flex-direction: column;
        height: auto;
        margin-bottom: 0.1rem;
        &:last-child {
          border-radius: 0 0 5px 5px;
        }
        a {
          font-family: "Monda", sans-serif;
          font-size: 15px;
          &:hover {
            background: $dropDownMenu_color;
            color: $header_links_color;
          }
        } 
      }
    }
  }
}
@mixin nav_menu_components_laptop_and_desktop {
  .big_box_components {
    display: flex;
    justify-content: space-evenly;
    margin: 0 2rem;
    border-radius: 0 0 5px 5px;
    background: $primery_color;
    .navProductsParrent {
      height: 40px;
      width: 220px; 
      .link_menu {
        text-align: center;
        padding: 3px;
        a {
          text-decoration: none;
          color: black;
          font-family: "Teko", sans-serif;
          font-size: 23px;
          margin-top: 15px;
        }
      }
      .drop_down_menu_items { 
        display: none;
        li {
          list-style: none;
          a {
            float: none; 
            color: $header_links_color;
            text-decoration: none;
            padding: 10px;
            display: block;
            text-align: left;
            padding-left: 30px;
            position: relative;

            .fas {
              position: absolute;
              right: 3px;
              top: 17px;
              color: $header_background;
              font-size: 12px;
            }
          }
        }
        .drop_down_menu_items_item {
          display: none;
        }
        .drop_down_menu_items_item {
          display: block;
          .btn_items_item {
            font-family: "Teko", sans-serif;
            width: 100%;
            color: $header_links_color;
            font-size: 18px;   
            border: none; 
            text-align: left;
            background: whitesmoke; 
            padding: 4px 0 0 15px; 
            cursor: pointer; 
            &:hover {
              background: rgba(176, 221, 233, 0.904);
            }
          }
        }
      } 
      .drop_down_menu_items { 
        background: white;
        position: relative;
        display: block;
        display: flex; 
        flex-direction: column;
        height: auto;
        z-index: 2;
        &:last-child { 
          border-radius: 0 0 5px 5px;
        }
        a {
          font-family: "Monda", sans-serif;
          font-size: 15px;
          &:hover {
             background: $dropDownMenu_color;
            color: $header_links_color; 
          }
        } 
      }
    }
  }
}

@mixin current_component_clicked_on_parrent {
  .current_component_clicked_on_parrent {
    background: white; 
    border-radius: 5px;
    opacity: 0.9;
    .current_component_name {
      height: 70px;
      border-bottom: 1px solid $header_links_color;
      h2 {
        padding: 2rem 1rem;
        color: $header_links_color;
        font-family: "Arvo", serif;
      }
    }
    .current_component_childs {
      display: flex;
      .article_childs {
        width: 30%;
        background-color: $header_background;
        border-radius: 0 0 0 5px;
        .name_child {
          border-bottom: 1px solid $header_links_color;
          a {
            text-decoration: none;
            color: rgb(8, 8, 8);
            font-family: "Teko", sans-serif;
            display: block;
            padding: 9px;
            opacity: default;
            &:hover {
              background: $component_background;
              transition: all 0.2s ease-out;
            }
          }
        }
      }
      .nameInfo_current_component_childs {
        width: 70%;
        text-align: center;
        .info_child {
          display: flex;
          .nameInfo_current_component_childs_image {
            width: 50%;
            img {
              margin-top: 1rem;
              max-width: 70%;
              max-height: 70%;
            }
          }
          .product_specifications {
            background-color: rgb(233, 230, 230);
            margin: 1rem;
            width: 50%;
            h4 {
              margin: 1rem auto;
            }
          }
        }
        .price_and_buyBtn {
          display: flex;
          justify-content: center;
          margin: 2rem;
          .btn_buy {
            @include btn_buy;
          }
        }
      }
    }
  }
}