@import "./mixin";
@import "./variables_color";

* {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
}

body {
  background-color: #e0e0e0;
}

.side_drawer {
  height: 100%;
  background: $header_background;
  position: fixed;
  top: 0;
  left: 0;
  width: 60%;
  height: auto;
  z-index: 200;
  transform: translateY(-100%);
  transition: transform 0.3s ease-out;

  &.open {
    transform: translateY(0);
    box-shadow: 2px 0 5px black;
  }

  ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    li {
      margin: 0.5rem 0;
    }
    a {
      font-size: 1.2rem;
      color: $header_links_color;
      transition: all 0.2s ease-in;
      font-family: "Monda", sans-serif;
      text-decoration: none;
      text-transform: uppercase;

      &:hover {
        color: white;
      }
    }
  }
  .btn_hide_siteDrawer {
    float: right;
    margin: 0 5px 5px 0;
    text-decoration: none;
    border: none;
    background: $header_background;
    cursor: pointer;
    .fas {
      font-size: 22px;
      color: $header_links_color;
    }
  }
  .count_products {
    color: rgb(175, 14, 14);
    font-size: 16px;
    font-weight: bold;
  }
}

.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 100;
}

header {
  background: $header_background;
  width: 100%;

  .toolbar_navigation {
    align-items: center;
    display: flex;
    .toggleBtn_logo {
      display: flex;
      margin: 0 0.5rem;
      align-items: center;
      .toggle_button {
        margin-right: 10px;

        height: 30px;
        width: 30px;
        border: none;
        background: $header_background;
        cursor: pointer;
        padding: 1px 0;
        box-sizing: border-box;

        &:focus {
          outline: none;
        }
        .toggle_button_line {
          width: 100%;
          height: 3px;
          background: white;
          margin: 5px 0;
        }
      }
      .toolbar_logo {
        img {
          margin-top: 4px;
          border-radius: 40px;
          width: 35px;
          height: 35px;
        }
      }
    }

    .toolbar_navigations_items {
      width: 60%;
      ul {
        width: 85vw;
        display: flex;
        li {
          padding: 6px 0;
          list-style: none;
          margin: 0 1rem;
          //border: 2px solid red;
          a {
            color: $header_links_color;
            font-family: "Monda", sans-serif;
            text-decoration: none;
            text-transform: uppercase;
            font-size: 15px;
            font-weight: bold;
            padding: 3px;
            transition: all 0.2s ease-in;
          }
        }
      }
    }
    .cart {
      li {
        // border: 2px solid red;
        padding: 8px;
        list-style: none;
        position: absolute;
        top: 2px;
        right: 6px;
        a {
          color: $header_links_color;
          font-family: "Monda", sans-serif;
          text-decoration: none;
          text-transform: uppercase;
          font-size: 13px;
          font-weight: bold;
          padding: 3px;
          transition: all 0.2s ease-in;
        }
        .count_products {
          color: rgb(175, 14, 14);
          font-size: 16px;
          font-weight: bold;
        }
      }
    }
  }
}

.background_image_cart {
  padding: 2rem 0;
  height: auto;
  min-height: calc(100vh - 20px);
  @include background_image_cart();

  border-radius: 0 0 5px 5px;

  .res_tab {
    overflow-x: auto;
    background-color: white;
    border-radius: 3px;
    height: auto;
    width: 80%;
    margin: 2rem auto;
  }

  table {
    border-collapse: collapse;
    width: 100%;
    margin: 0.5rem 0 3rem 0;
    text-align: center;
    font-style: italic;
    position: relative;

    .fixed_name_length {
      width: 30%;
    }

    th {
      background-color: $backdrond_table;
      color: $header_links_color;
      font-family: "Monda", sans-serif;
      font-size: 14px;
    }
    td {
      background-color: white;

      .quantity_remove {
        margin-left: 15px;
        font-size: 20px;
        color: rgb(73, 71, 71);
      }
    }
    span {
      font-weight: bold;
    }

    th,
    td {
      border: 1px solid $color_cart_items;
      padding: 5px;
      margin: 0;
      img {
        width: 80px;
        height: 80px;
      }
      a {
        text-decoration: none;
        &:hover {
          color: red;
        }
      }
    }
    .order_total {
      position: absolute;
      margin-top: 7px;
      right: 10px;

      span {
        font-family: "Monda", sans-serif;
        font-size: 19px;
      }
    }
  }
  .empty_text {
    display: block;
    font-weight: bold;
    text-align: center;
    margin-bottom: 1rem;
    font-family: "Monda", sans-serif;
  }
}
.home {
  min-height: calc(100vh - 20px); 
  .center_image_home {
    img {
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      height: 100vh;
      width: 100%;
    }
    .links_products_home {
      display: flex;
      justify-content: space-evenly;
      position: absolute;
      bottom: 1rem;
      left: 0;
      right: 0;
      margin: 0 auto;
      .link_item_product_laptops,
      .link_item_product_desktops,
      .link_item_product_vr,
      .link_item_product_configute {
        img {
          background-position: center center;
          background-repeat: no-repeat;
          background-size: cover;
          opacity: 0.8;
          width: 200px;
          height: 300px;
        }
      }
    }
  }
}

.background_Games {
  padding: 2rem 0;
  height: auto;
  min-height: calc(100vh - 20px);
  background: white;
  border-radius: 0 0 5px 5px;
  margin: 0 2rem 1rem 2rem;

  .all_filters {
    .lenght_games,
    .ordeyBy {
      display: inline-block;
      margin-left: 1rem;
      label {
        span {
          font-family: "Monda", sans-serif;
        }
      }
    }
    .lenght_games {
      margin-right: 1rem;
      background: white;
      border-radius: 2px;
      h4 {
        padding: 5px;
        color: black;
        border: 1px solid $header_background;

        span {
          font-size: 13px;
          font-family: "Monda", sans-serif;
        }
      }
    }
    .ordeyBy {
      background-color: white;
      padding: 5px;
      border-radius: 2px;

      z-index: 1;
      .drop_down_ordeyBy {
        margin-left: 5px;
        width: 130px;
      }
    }
  }

  .all_boxes {
    z-index: 0;
    border-bottom: 1px solid $border;
    margin: 0 2rem;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

    .box {
      width: 200px;
      height: 300px;
      margin: 1rem;
      color: white;
      position: relative;
      background-color: white;
      text-align: center;

      .imageGame {
        margin-top: 1rem;

        img {
          width: 200px;
          height: 200px;
        }
      }
      span,
      h3 {
        color: black;
      }
      .buttons {
        margin: 4px 0;
        a {
          padding: 2px;
          text-decoration: none;
          border-radius: 4px;
          color: white;
          background-color: $header_background;
          font-size: 13px;
          font-family: "Monda", sans-serif;
          text-transform: uppercase;
          &:hover {
            color: black;
          }
        }
        display: none;
      }

      &:hover {
        box-shadow: 4px 4px 18px grey;

        .buttons {
          display: block;
        }
      }
    }
  }
  .buttons_next_products {
    ul {
      display: flex;
      justify-content: center;

      li {
        list-style: none;
        margin: 0 3px;

        a {
          color: black;
          background: rgb(197, 196, 196);
          font-weight: bold;
          padding: 3px;
          text-decoration: none;
          font-size: 19px;

          &:hover {
            background: grey;
          }
        }
      }
    }
  }
}
.box_infoGame {
  background: white;
  height: auto;
  min-height: calc(100vh - 20px);
  // margin: 0 2rem 1rem 2rem;
  border-radius: 0 0 5px 5px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .imageGame {
    width: 60%;
    .test_img {
      //border: 2px solid blue;
      width: 50%;
      margin: 1rem auto;
      img {
        //border: 2px solid orange;
        width: 100%;
        height: 100%;
      }
    }
  }

  .box {
    width: 70%;
    height: auto;
    margin-bottom: 1rem;
    background: white;
    border-radius: 4px;
    .infoGame {
      text-align: center;
      & > span {
        font-weight: bold;
        font-size: 20px;
        margin-bottom: 2rem;
      }
      .infoGame_cart {
        margin: 1rem 0;
      }
      .price_and_btn {
        margin-bottom: 1rem;
        display: flex;
        justify-content: center;
        .btn_buy {
          @include btn_buy;
        }
      }
    }
  }
  .box_btn_back {
    margin-bottom: 2rem;
    text-align: center;

    .btn_back {
      display: block;
      @include btn_back();

      span:hover {
        color: white;
      }
    }
  }
}

.background_image_ComputerHardWare {
  height: auto;
  min-height: calc(100vh - 20px);
  @include background_image_computerHardWare();
  @include current_component_clicked_on_parrent();
}
.background_image_laptops {
  height: auto;
  min-height: calc(100vh - 20px);
  @include background_image_laptops();
  @include current_component_clicked_on_parrent();
}
.background_contacts {
  min-height: calc(100vh - 20px);
  height: auto;
  background: white;
  border-radius: 0 0 5px 5px;

  .main_contacts {
    display: flex;
    flex-direction: column;

    .directions,
    .how_to_find_us,
    .opening_times {
      margin-top: 4rem;
    }
    .directions {
      text-align: center;
      border-bottom: 1px solid $border;
      margin: 1rem 10px;
      h2 {
        font-family: "Monda", sans-serif;
      }
      span {
        display: block;
        padding: 1rem 0;
        color: $header_links_color;
        font-size: 15px;
        font-family: "Monda", sans-serif;
      }
    }
    .how_to_find_us {
      padding: 0 10px;
      h2 {
        font-family: "Monda", sans-serif;
      }
      span {
        display: block;
        color: $header_links_color;
        font-size: 14px;
      }
      .our_addres {
        color: $header_links_color;
        font-size: 13px;
        font-weight: bold;
      }
      .contacts_image {
        margin: 0.5rem 0 3rem 0;
        img {
          width: 100%;
        }
      }
      .info {
        display: block;
        text-align: center;
        span {
          margin: 1rem 0;
        }
      }
    }
    .opening_times {
      h2 {
        padding: 1rem 0;
        font-family: "Monda", sans-serif;
      }
      span {
        display: block;
        color: $header_links_color;

        font-weight: bold;
        padding: 1rem;
        border: 1px solid $border;
      }
      .table_info {
        display: flex;
        .box_days,
        .store_times,
        .call_times {
          width: 100%;
          text-align: center;
          span {
            color: $header_links_color;
            position: relative;
          }
          .line_01 {
            background: $backdrond_table;
            color: $header_links_color;
            position: relative;
          }
          .line_02 {
            background: white;
            color: $header_links_color;
            position: relative;
          }
        }
      }
    }
    p {
      margin: 2rem 0;
      text-align: center;
      font-size: 15px;
      color: $header_links_color;
      font-weight: bold;
    }
    .contacts_form {
      display: flex;
      margin-bottom: 1rem;

      .form {
        &:hover {
          box-shadow: 1px 1px 6px grey;
        }

        h2 {
          font-size: 20px;
          font-family: "Monda", sans-serif;
        }
        width: 100%;
        padding: 0 0.5rem 1rem 0.5rem;
        .name_on_form {
          color: $header_links_color;
        }
        span {
          display: block;
          .star_nameForm {
            color: $header_background;
            display: inline-block;
          }
        }
        input {
          width: 50%;
          height: 35px;
          opacity: 0.6;
        }
        input[type="text"]:focus {
          background: #d4e4f2;
          box-shadow: 1px 1px 7px grey;
          padding-left: 10px;
        }
        .comment {
          width: 70%;
          height: 85px;
        }
        .btn_request {
          text-decoration: none;
          font-size: 14px;
          padding: 5px;
          background: #837f7f;
          color: white;
          font-family: "Monda", sans-serif;

          &:hover {
            background: $backgraund_color_buy;
          }
        }
      }
      .contactsAndFollowUs {
        width: 30%;
        h3 {
          font-family: "Monda", sans-serif;
          text-align: center;
        }

        .links_follow_us {
          text-align: center;
          display: flex;
          flex-direction: column;
          a {
            margin: 0 0.5rem;
            color: $backgraund_color_buy;
            font-size: 25px;
            padding: 5px 7px;

            &:hover {
              color: $header_background;
            }
          }
        }
      }
    }
  }
}
.background_notFound {
  min-height: calc(100vh - 20px);
  background: white;

  .info_box {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: 3rem auto;
    text-align: center;

    a {
      margin: 0 auto;
      text-decoration: none;
      color: black;
      border-bottom: 2px solid black;
    }
  }
}

.footer {
  text-align: center;
  height: 30px;
  padding: 2px;
  color: white;
  background-color: $footer_color;
  p {
    font-family: "Monda", sans-serif;
  }
  span {
    color: $header_background;
  }
}
 
@media screen and(max-width: 450px) {
  .toolbar_navigations_items {
    display: none;
  }
  @include nav_menu_components_phone_and_tablet();
  .opening_times {
    span {
      font-size: 50%;
      color: red;
    }
  }
  .background_contacts,
  .main_contacts,
  .background_image_cart,
  .box_infoGame {
    margin: 0;
  }
  .home {
    .center_image_home {
      .links_products_home {
        display: flex;
        flex-wrap: wrap;
        bottom: 0;
        .link_item_product_laptops,
        .link_item_product_desktops,
        .link_item_product_vr,
        .link_item_product_configute {
          img {
            width: 80px;
            height: 160px;
          }
        }
      }
    }
  }

  .error_box {
    h1 {
      font-size: 60px;
    }
  }
  .img_error_box {
    img {
      width: 100px;
    }
  }
  .current_component_clicked_on_parrent {
    margin-top: 2rem;
    .name_child {
      a {
        font-size: 16px;
      }
    }
    .nameInfo_current_component_childs {
      h3 {
        font-size: 16px;
      }
    }
  }
}

@media screen and(min-width: 451px) and(max-width: 768px) {
  .toolbar_navigations_items {
    display: none;
  }
  @include nav_menu_components_phone_and_tablet();
  .opening_times {
    span {
      font-size: 70%;
      color: red;
    }
  }
  .background_contacts,
  .box_infoGame {
    margin: 0;
  }
  .main_contacts {
    margin: 0 1rem;
  }

  .tollbar_toggle_button {
    display: show;
  }
  .home {
    .center_image_home {
      .links_products_home {
        .link_item_product_laptops,
        .link_item_product_desktops,
        .link_item_product_vr,
        .link_item_product_configute {
          img {
            width: 100px;
            height: 200px;
          }
        }
      }
    }
  }

  .error_box {
    h1 {
      font-size: 140px;
    }
  }
  .current_component_clicked_on_parrent {
    margin: 2rem;
    .name_child {
      a {
        font-size: 19px;
      }
    }
    .nameInfo_current_component_childs {
      h3 {
        font-size: 19px;
      }
    }
  }
}

@media screen and (min-width: 768px) and(max-width: 1170px) {
  .background_contacts,
  .background_image_cart,
  .box_infoGame {
    margin: 0 2rem 1rem 2rem;
  }
  @include nav_menu_components_laptop_and_desktop();
  .opening_times {
    span {
      font-size: 80%;
      color: red;
    }
  }
  .main_contacts {
    margin: 0 7rem;
  }

  .tollbar_toggle_button {
    display: none;
  }
  .home {
    .center_image_home {
      .links_products_home {
        .link_item_product_laptops,
        .link_item_product_desktops,
        .link_item_product_vr,
        .link_item_product_configute {
          img {
            width: 150px;
            height: 250px;
          }
        } 
      }
    }
  }
  .error_box {
    h1 {
      font-size: 140px;
    }
  }
  .current_component_clicked_on_parrent {
    margin: 6rem 8rem 6rem 8rem;
    .name_child {
      a {
        font-size: 19px;
      }
    }
    .nameInfo_current_component_childs {
      h3 {
        font-size: 19px;
      }
    }
  }
}

@media screen and (min-width: 1171px) {
  .background_contacts,
  .background_image_cart,
  .box_infoGame {
    margin: 0 2rem 1rem 2rem;
  }
  @include nav_menu_components_laptop_and_desktop();
  .main_contacts {
    margin: 0 10rem;
  }
  .tollbar_toggle_button {
    display: none;
  }
  .links_products_home {
  }

  .error_box {
    h1 {
      font-size: 140px;
    }
  }
  .current_component_clicked_on_parrent {
    margin: 6rem 8rem 6rem 8rem;
    .name_child {
      a {
        font-size: 19px;
      }
    }
    .nameInfo_current_component_childs {
      h3 {
        font-size: 19px;
      }
    }
  }
}
